<template>
  <div
    class="tulip-scroller-wrapper"
    ref="scrollWrapper"
  >
    <div
      ref="scroll"
      class="tulip-scroller tulip-scroller-hardware"
      :style="{paddingTop:0+'px',...wrapStyle}"
    >
      <div class="above-top">
        <slot name="aboveTop"></slot>
      </div>
      <slot
        name="aboveWrap"
        :aboveWrapHeight="aboveWrapHeight"
      >
        <div
          class="above-wrap tulip-scroller-hardware"
          ref="above"
          v-show="above.isBounce"
          :style="{...aboveWrapStyle}"
        >
          <div
            class="above-content"
            :style="{justifyContent:above.align,}"
          >
            <slot
              name="above"
              :aboveWrapHeight="aboveWrapHeight"
              :aboveState="aboveState"
              :isAboveNoMore="isAboveNoMore"
            >
              <div
                class="above-inner"
                v-if="above.style==='default'"
              >
                <original
                  :y="aboveWrapHeight"
                  :offset="above.offset|| above.hoverHeight"
                  :aboveState="aboveState"
                />
              </div>
              <div v-else-if="above.style==='solo'">
                <solo
                  :y="aboveWrapHeight"
                  :above="true"
                  :textShow="isAboveNoMore"
                  :offset="above.offset|| above.hoverHeight"
                  :aboveState="aboveState"
                />
              </div>
              <div v-else-if="above.style==='classic'">
                <classic
                  :y="aboveWrapHeight"
                  :offset="above.offset||above.hoverHeight"
                  :aboveState="aboveState"
                />
              </div>
            </slot>
          </div>
        </div>
      </slot>
      <!-- content -->
      <div
        class="tulip-scroller-content"
        ref="scrollContent"
      >
        <div
          class="tulip-scroller-inner"
          ref="scrollInner"
        >
          <slot></slot>
        </div>
        <!-- below-wrap -->
        <div
          class="below-wrap"
          ref="below"
        >
          <slot
            name="below"
            :belowState="belowState"
            :isBelowNoMore="isBelowNoMore"
          >
            <div v-show="below.style==='default'">
              <div v-show="belowState==='loading'&&!isBelowNoMore">
                <slot name="below"></slot>
              </div>
              <div v-show="isBelowNoMore">
                <slot name="isBelowEnd"></slot>
              </div>
            </div>
            <div v-show="below.style==='solo'||below.style==='classic'">
              <solo
                :belowState="belowState"
                :textShow="isBelowNoMore"
                :below="true"
              />
            </div>
          </slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import solo from './solo'
import classic from './classic'
import original from './original'
import Animation from './animate'
const aboveOpt = {
  isBounce: true,
  style: 'none',
  easeTime: 300,
  top: 0,
  align: 'flex-end', // start center end 
  isLock: false, // 
  isEndless: false, // ()
  offset: 90, // [50,100]
  hoverHeight: 0, // [0,100]
  hoverDelay: 0, // [0,2000]
  inOffsetRate: 0.6, // [0.2,0.8]
  outOffsetRate: 0.3, //  [0.2,0.8]
  minAngle: 45, //  [30,60]
  bottomOffset: 0, //  [0,100]
  pullingDown: pullingY => { }, // 
  inOffset: type => { }, // 
  outOffset: (type, done) => {
    // done()
  }, // 
  pullingEnd: done => {
    done()
    return true // return true 
  } // 
}

const belowOpt = {
  style: 'none',
  isLock: false, // 
  threshold: 90, // [50,100]
  callback: done => {
    setTimeout(() => {
      done()
    }, 1000)
  }
}
export default {
  name: 'tulipScroll',
  components: {
    solo,
    classic,
    original
  },
  props: {
    aboveOpt: {
      type: Object,
      default: () => {
        return {}
      }
    },
    belowOpt: {
      type: Object,
      default: () => {
        return {}
      }
    },
    wrapStyle: {
      type: Object,
      default: () => {
        return {}
      }
    },
    aboveWrapStyle: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      // 
      animateIds: [],
      // 
      clientHeight: 0,
      // 
      scrollTop: 0,
      startPoint: 0, // 
      lastPoint: 0, // 
      isScrollTo: false, // 
      isTouchend: true, // touchend
      aboveWrapHeight: 0, // 
      aboveState: 'over', // 
      belowState: 'over',
      beLowLoading: false,
      isBelowLoadingEnd: false,
      isBelowNoMore: false,
      isAboveNoMore: false,
      animate: null// 
    }
  },
  computed: {
    above () {
      return { ...aboveOpt, ...this.aboveOpt }
    },
    below () {
      return { ...belowOpt, ...this.belowOpt }
    },
    // 
    os () {
      var u = navigator.userAgent
      var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios
      var isPC = typeof window.orientation === 'undefined' // PC
      var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1 // android

      return {
        ios: isIOS,
        pc: isPC,
        android: isAndroid
      }
    }
  },
  watch: {
    $route: {
      handler (val) {
        if (val) {
          this.setScrollTop(this.scrollTop)
        }
      }
    }
  },
  mounted () {
    this.scrollDom = this.$refs.scroll
    this.scrollWrapperDom = this.$refs.scrollWrapper
    this.aboveDom = this.$refs.above
    this.scrollContentDom = this.$refs.scrollContent
    this.watchDom(this.scrollDom)
    this.clientHeight = this.scrollDom.clientHeight
    this.belowDom = this.$refs.below
    this.initScroll()
  },
  methods: {
    initScroll () {
      let vm = this
      vm.scrollDom.addEventListener('mousedown', vm.touchstartEvent) // PC
      vm.scrollDom.addEventListener('touchstart', vm.touchstartEvent, {
        passive: false
      }) // 
      vm.scrollDom.addEventListener('touchmove', vm.touchmoveEvent, {
        passive: false
      })
      // 
      vm.scrollDom.addEventListener('mouseup', vm.touchendEvent) // PC
      vm.scrollDom.addEventListener('mouseleave', vm.touchendEvent) // PC
      vm.scrollDom.addEventListener('touchend', vm.touchendEvent, {
        passive: false
      }) // 
      vm.scrollDom.addEventListener('touchcancel', vm.touchendEvent, {
        passive: false
      }) // 
      vm.scrollDom.addEventListener('scroll', vm.scrollEvent, {
        passive: false
      }) // 
      // 
      vm.$emit('init', vm)
    },
    /**
     * 
     * @param {*} e 
     * scrollTo,,scrollTo
     * 
     * move
     * 
     * touchend
     * PCmovePC
     * PC,
     */
    touchstartEvent (e) {
      var vm = this
      // 
      if (vm.isScrollTo && !vm.above.isEndless) {
        vm.startPoint = null
        vm.preventDefault(e)
        return
      }
      vm.startPoint = vm.getPoint(e)
      vm.lastPoint = vm.startPoint
      vm.maxTouchmoveY = vm.getBodyHeight() - vm.above.bottomOffset
      vm.isTouchend = false
      let { scrollTop } = vm.getScrollInfo()
      // pc
      if (vm.os.pc && scrollTop <= 0) {
        vm.scrollDom.addEventListener('mousemove', vm.touchmoveEvent, {
          passive: false
        })
        document.ondragstart = () => {
          return false
        }
      }
    },
    /**
     * 
     * @param {*} e 
     * isScrolling 
     * moveY > 0 
     */
    touchmoveEvent (e) {
      let vm = this
      if (!vm.startPoint) return

      vm.isScrolling = false
      vm.setBounce(false)
      let { scrollTop } = vm.getScrollInfo()
      vm.scrollTop = scrollTop
      var curPoint = vm.getPoint(e) // 
      var moveY = curPoint.y - vm.startPoint.y // ,,0,0
      vm.moveY = moveY
      if (moveY > 0) {
        if (scrollTop <= 0) {
          vm.pullingDownEvent(curPoint, e)
        }
      } else if (moveY < 0) {
        if (vm.toBottom() <= 0) {
          vm.preventDefault(e)
          vm.setOverflowScrolling('auto')
          if (vm.below.belowState === 'over') {
            vm.scrollLoad()
          }
        }
      }
      this.$emit('pulling', {
        e: e,
        moveY: moveY
      })
      vm.lastPoint = curPoint // 
    },
    /**
    * 
    * 
    * 
    * scrolling
    */
    scrollEvent (e) {
      let vm = this
      let { scrollTop, scrollHeight } = vm.getScrollInfo()
      vm.scrollTop = scrollTop
      if (parseInt(scrollTop) <= 0) {
        vm.preventBounce(e)
        vm.setBounce(false)
      }
      let scrollDiff = scrollTop - vm.preScrollY
      let isUp = scrollDiff > 0
      let toBottom = vm.toBottom()
      if (isUp && toBottom <= vm.below.threshold && vm.belowState !== 'loading') {
        vm.preventBounce(e)
        vm.scrollLoad()
      }
      this.$emit('scroll', { e, scrollTop, scrollHeight })
      if (vm.isTouchend) {
        vm.$emit('scrolling', { e, scrollTop, scrollHeight })
        vm.isScrolling = true
      }
      vm.preScrollY = scrollTop
    },
    stopAnimate () {
      let vm = this
      if (vm.animateIds.length) {
        vm.animateIds.map(v => {
          cancelAnimationFrame(v)
          this.cancelAnimationFrameStop = true
        })
        vm.animateIds = []
      }
    },
    /**
     *  
     * @param {*} curPoint 
     * @param {*} e 
     *  vm.above.isBounce?vm.aboveWrapHeight:0;above.isBounce=falseabove.isBounce=falseisLock=false
     * 
     * 
     * overpullingDown
     * ,,Webviewtouchend
     * diff , (0,0)
     */
    pullingDownEvent (curPoint, e) {
      let vm = this
      if (vm.above.isLock) {
        // 
        vm.startPoint = null
        vm.preventDefault(e)
        return
      }
      if (vm.os.ios) {
        vm.setOverflowScrolling('auto')
      }
      // bounce
      vm.preventBounce(e)
      if (vm.inAngle(curPoint)) {
        vm.setAboveState('pullingDown')
        if (vm.aboveState !== 'loading' && vm.aboveState !== 'hovering') {
          if (vm.maxTouchmoveY > 0 && curPoint.y >= vm.maxTouchmoveY) {
            vm.touchendEvent()
            return
          }
          var diff = curPoint.y - vm.lastPoint.y
          if (!vm.aboveWrapHeight) vm.aboveWrapHeight = 0

          // 
          if (diff > 0) {
            //   < 
            if (vm.aboveWrapHeight < vm.above.offset) {
              vm.above.inOffset()
              vm.aboveWrapHeight += diff * vm.above.inOffsetRate
              clearTimeout(vm.endAboveTimer)
              vm.endAboveTimer = null
            } else {
              vm.above.outOffset()
              vm.aboveWrapHeight += diff * vm.above.inOffsetRate
            }
          } else {
            vm.aboveWrapHeight += diff
            if (vm.aboveWrapHeight <= 0) vm.aboveWrapHeight = 0
          }
          requestAnimationFrame(() => {
            vm.aboveDom.style.height = vm.aboveWrapHeight + 'px'
          })
          var rate = vm.aboveWrapHeight / vm.above.offset // 
          vm.above.pullingDown(vm.aboveWrapHeight, rate) // ,
        }
      }
    },
    // iosbounce
    preventBounce (e) {
      this.preventDefault(e)
      this.setOverflowScrolling('auto')
    },
    setOverflowScrolling (type) {
      let vm = this
      if (type === 'auto') {
        vm.scrollDom.style.webkitOverflowScrolling = 'auto' // ,vm.aboveDom.style.height,
        vm.isSetScrollAuto = true // webkitOverflowScrollingauto
      } else {
        vm.scrollDom.style.webkitOverflowScrolling = 'touch'
        vm.isSetScrollAuto = false
      }
    },
    scrollLoad () {
      this.belowState = 'loading'
      // 
      this.below.callback(this.loadedEnd)
    },
    loadedEnd (flag) {
      this.isBelowNoMore = !!flag
      this.belowState = 'over'
    },
    /**
     * vm.above.hoverHeight 
     * vm.above.offset 
     * vm.above.offset>=vm.above.hoverHeight
     * vm.above.hoverHeight,hoverHeight = above.offset
     * resetAboveHeightToZero offset
     * aboveWrapHeight 
     * 
     *  h
     *  h
     */
    resetAboveHeightToZero () {
      let vm = this
      vm.raf(vm.aboveWrapHeight, 0, (value, flag) => {
        vm.isScrollTo = true
        if (flag) {
          // 
          vm.isScrollTo = false
        }
      })
    },
    // 
    /**
     * 
     * @param {*} e
     */
    touchendEvent (e) {
      let vm = this
      vm.isTouchend = true // touchend
      // ,
      if (vm.aboveState === 'pullingDown') {
        if (vm.aboveWrapHeight >= vm.above.offset) {
          // 
          vm.triggerAboveLoad()
        } else {
          //  
          vm.resetAboveHeightToZero()
        }
        if (vm.isSetScrollAuto) {
          vm.setOverflowScrolling('touch')
        }
        vm.movetype = 0
      }

      if (vm.belowState === 'over' && vm.toBottom() === 0) {
        // 
        vm.scrollLoad()
      }
      if (vm.os.pc) {
        vm.scrollDom.removeEventListener('mousemove', vm.touchmoveEvent) // pcmove
        document.ondragstart = function () {
          return true // PC
        }
      }
    },
    /*  type  */
    triggerAboveLoad (type) {
      let vm = this
      vm.type = type
      // skipAboveDelay=trueabove.pullingEnd 
      /**
       * this.above.pullingEndtrue,0
       * this.above.pullingEndthis.skipAboveDelay=undefinedtrue
       */
      this.hasAboveCallback = false // 
      // 

      this.skipAboveDelay = this.above.pullingEnd(this.endAboveScroll)
      if (!type) {
        this.showAboveIndicator() //  ...
      }
    },
    refreshAboveState () {
      this.isAboveNoMore = false
    },
    /*  */
    /**
     *
     * @param {*} doneFlag false --no more--
     */
    endAboveScroll (doneFlag) {
      var vm = this
      vm.hasAboveCallback = true
      vm.isAboveNoMore = !!doneFlag
      vm.setAboveState('hovering')
      if (vm.isAboveNoMore) {
        vm.resetAboveHeightToZero()
        return
      }
      if (vm.aboveState === 'pullingDown') {
        return
      }
      // 
      var endScroll = function () {
        // isEndless
        if (vm.aboveState === 'hovering') {
          vm.resetAboveHeightToZero()
          vm.isBelowNoMore = false
        }
      }

      // 
      let delay = vm.above.hoverDelay

      if (vm.aboveState !== 'pullingDown') {
        vm.setAboveState('hovering')
      }
      if (typeof delay === 'number' && delay > 0) {
        vm.endAboveTimer = setTimeout(endScroll, delay)
      } else {
        endScroll()
      }

      this.$nextTick(() => {
        // 
        // 
        if (this.scrollContentDom.clientHeight <= this.clientHeight) {
          this.scrollLoad()
        }
      })
    },
    watchDom (dom) {
      let config = {
        attributes: true,
        childList: false,
        subtree: false,
        attributeFilter: ['style']
      }
      this.domObserver().observe(dom, config)
    },
    // dom MutationObserver
    domObserver () {
      let MutationObserver =
        window.MutationObserver ||
        window.WebKitMutationObserver ||
        window.MozMutationObserver
      let vm = this
      var observer = new MutationObserver(function (mutations) {
        mutations.forEach(function (mutation) {
          if (mutation.type === 'attributes') {
            vm.clientHeight = vm.scrollDom.clientHeight
          }
        })
      })
      return observer
    },
    /**
     * 
     */
    triggerBelowLoad () {
      if (this.isBelowNoMore) return
      this.scrollLoad()
    },
    /**
     * 
     * trigger 
     *
     */
    showAboveIndicator (trigger) {
      let vm = this
      if (vm.hasAboveCallback) {
        // pullingEnd
        return
      }
      let targetHeight = this.above.hoverHeight || this.above.offset
      if (vm.aboveState !== 'hovering') {
        vm.setAboveState('loading')
      }
      let finalHeight = this.skipAboveDelay ? 0 : targetHeight
      vm.isScrollTo = true
      if (this.skipAboveDelay) {
        this.raf(this.aboveWrapHeight, 0, (value, flag) => {
          this.aboveWrapHeight = value
        })
      } else {
        if (trigger) {
          this.raf(0, finalHeight, (value, flag) => {
            this.aboveWrapHeight = value
          })
        } else {
          this.raf(this.aboveWrapHeight, finalHeight, (value, flag) => {
            this.aboveWrapHeight = value
          })
        }
      }
    },
    /*  */
    preventDefault (e) {
      // cancelable:; defaultPrevented:
      if (e && e.cancelable && !e.defaultPrevented) e.preventDefault()
    },
    /*  */
    getPoint (e) {
      return {
        x: e.touches ? e.touches[0].pageX : e.clientX,
        y: e.touches ? e.touches[0].pageY : e.clientY,
        t: e.timeStamp ? e.timeStamp : 0
      }
    },
    /* body */
    getBodyHeight () {
      return (
        document.body.clientHeight || document.documentElement.clientHeight
      )
    },
    getScrollInfo () {
      return {
        scrollTop: this.scrollDom.scrollTop,
        scrollHeight: this.scrollDom.scrollHeight
      }
    },
    // 
    toBottom () {
      let { scrollTop, scrollHeight } = this.getScrollInfo()
      return parseInt(scrollHeight - this.clientHeight - scrollTop)
    },
    // 
    inAngle (curPoint) {
      let vm = this
      // 
      var x = Math.abs(vm.lastPoint.x - curPoint.x)
      var y = Math.abs(vm.lastPoint.y - curPoint.y)
      var z = Math.sqrt(x * x + y * y)
      if (z !== 0) {
        var angle = (Math.asin(y / z) / Math.PI) * 180 // , [0,90]
        if (angle < vm.above.minAngle) return false // ,
      }
      return true
    },
    //   pullingDown  loading[loading] hovering over pullingUp beLowLoading[loading] end
    setAboveState (type) {
      this.aboveState = type
    },
    setBelowState (state) {
      // beLowLoading[loading] end
      this.belowState = state
    },
    setBounce (isBounce) {
      if (!this.os.ios) return
      if (isBounce === false) {
        // 
        window.addEventListener('touchmove', this.bounceTouchmove, {
          passive: false
        })
      } else {
        // 
        window.removeEventListener('touchmove', this.bounceTouchmove)
      }
    },
    /**
     * touchtouchmove
     * @param {*} e 
     */
    bounceTouchmove (e) {
      var vm = this
      var el = e.target
      var isPrevent = true
      while (el !== document.body && el !== document) {
        // Ignore range input element
        if (el.nodeName === 'INPUT' && el.getAttribute('type') === 'range') {
          return
        }
        var cls = el.classList
        if (cls) {
          if (cls.contains('tulip-scroller') || cls.contains('scroll-touch')) {
            isPrevent = false // ,touchmove
            break
          } else if (
            cls.contains('scroll-touch-x') ||
            cls.contains('scroll-touch-y')
          ) {
            // 
            var curX = e.touches ? e.touches[0].pageX : e.clientX // x
            var curY = e.touches ? e.touches[0].pageY : e.clientY // y

            if (!vm.preWinX) vm.preWinX = curX // x
            if (!vm.preWinY) vm.preWinY = curY // y

            // 
            var x = Math.abs(vm.preWinX - curX)
            var y = Math.abs(vm.preWinY - curY)
            var z = Math.sqrt(x * x + y * y)

            vm.preWinX = curX // curX
            vm.preWinY = curY // curY

            if (z !== 0) {
              var angle = (Math.asin(y / z) / Math.PI) * 180 //  [0,90]
              if (
                (angle <= 45 && cls.contains('scroll-touch-x')) ||
                (angle > 45 && cls.contains('scroll-touch-y'))
              ) {
                isPrevent = false // ,touchmove
                break
              }
            }
          }
        }
        el = el.parentNode // 
      }

      // touchmove:&&
      if (
        isPrevent &&
        e.cancelable &&
        !e.defaultPrevented &&
        typeof e.preventDefault === 'function'
      ) {
        e.preventDefault()
      }
    },
    setScrollTop (y) {
      if (typeof y === 'number') {
        this.scrollDom.scrollTop = y
      }
    },
    /**
     *
     * @param {*} t 
     * @param {*} b 
     * @param {*} c 
     * @param {*} d 
     * 
     * offsethoverHeight0
     * aboveWrapHeighthoverHeightoffset
     * 
     *
     }
     */
    raf (from, to, cb) {
      let vm = this
      vm.isScrollTo = true
      vm.animate = Animation(
        {
          from,
          to,
          callback: function (value, flag) {
            cb && cb(value, flag)
            if (vm.animateIds.length) {
              vm.aboveDom.style.height = value + 'px'
              vm.aboveWrapHeight = value
            }
            if (flag) {
              if (to === 0) {
                vm.setAboveState('over')
              } else {
                vm.setAboveState('loading')
              }
              cancelAnimationFrame(vm.animate)
            }
          },
          duration: 200,
          easing: 'easeOut'
        }
      )
      vm.animateIds.push(vm.animate)
      return vm.animate
    }
  }
}
</script>

<style lang="less" scoped>
body {
  /* 
      */
  overscroll-behavior-y: none;
}
body {
  overflow: hidden;
  overscroll-behavior-y: none;
}
input {
  -webkit-user-modify: read-write-plaintext-only;
}
.above-top {
  font-size: 0px;
  position: relative;
  z-index: 9;
}
/*:,,,,*/
.tulip-scroller-hardware {
  -webkit-overflow-scrolling: auto;
  overscroll-behavior-y: none;
}
.tulip-scroller-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: auto;
  touch-action: none;
  overflow: hidden;
  z-index: 0;
}
.tulip-scroller {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  overflow-y: scroll;
  box-sizing: border-box;
  font-size: 24px;
  z-index: 1;
  -webkit-overflow-scrolling: auto;
  overscroll-behavior-y: none;

  .tulip-scroller-content {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    position: relative;
    z-index: 2;
    .tulip-scroller-inner {
      flex: 1;
    }
  }
  .above-wrap {
    position: relative;
    width: 100%;
    height: 0;
    text-align: center;
    transform: translateZ(0);
    transform-style: preserve-3d;
    backface-visibility: hidden;
    perspective: 1000;
    .above-content {
      position: absolute;
      top: -50px;
      left: 0;
      bottom: -50px;
      padding-top: 50px;
      width: 100%;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      height: 100%;
      .above-inner {
        display: flex;
        align-content: center;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 50px;
        margin: 0 auto;
        .success {
          display: none;
        }
      }
    }
  }

  .below-wrap {
    min-height: 40px;
    padding: 15px 0;
    text-align: center;
    flex: 0;
  }
}
</style>
