<template>
  <div class="default">
    <template v-if="aboveState=='pullingDown'">
      <template v-if="y<offset">
        <slot name="inOffsetInner"></slot>
      </template>
      <template v-else>
        <slot name="outOffsetInner"></slot>
      </template>
    </template>
    <template v-if="aboveState=='loading'">
      <slot name="refershing">...</slot>
    </template>
    <template v-if="aboveState=='hovering'">
      <slot name="hovering"></slot>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    y: Number,
    aboveState: String,
    offset: Number
  }
}
</script>

<style scoped>
.default {
  font-size: 14px;
  color: #777;
}
</style>
